<template>
    <div v-show="isSingleMode" class="appStatusContent p-r">
        <div class="search" v-show="isShowSearchInput"> 
            <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('appControl.keywordHolder')" >  
            </el-input>
            <i class="fa fa-search search-icon" aria-hidden="true"></i>
        </div>
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane name="appMarket">
                <span slot="label">
                    <i class="fa fa-sellsy m-r-5" aria-hidden="listLoading"></i>
                    {{$t('appControl.appMarket')}}
                </span>
                <el-table
                    :data="appTableData.filter(data => !keywords || data.appname.toLowerCase().includes(keywords.toLowerCase()))"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading">

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img :src="getSrc(scope.row.raid)" :onerror="defaultImg" alt="no img" style="width:52px"/>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="appname"
                    :label="$t('appControl.appName')"
                    min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="package"
                    :label="$t('appControl.packageName')"
                    min-width="120"
                    sortable>
                    </el-table-column>

                    <el-table-column
                    prop="version"
                    :label="$t('appControl.versionName')"
                    min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="type"
                    :label="$t('appControl.action')"
                    min-width="120"
                    :filters="[{ text: $t('appControl.uninstall'), value: 'uninstallApp' }, { text: $t('appControl.install'), value: 'installApp' }, { text: $t('appControl.upgrade'), value: 'upgradeApp' }]"
                    :filter-method="filterType"
                    filter-placement="bottom-end"
                    >
                        <template slot-scope="scope">
                            <div>
                                <span>
                                    <el-button-group>
                                        <el-button 
                                        size="small" 
                                        type="danger" 
                                        @click="appAction('uninstallApp', scope.row)"
                                        v-if="scope.row.type === 'uninstallApp'"
                                        :disabled="disableAppArray.indexOf(scope.row.package)>-1"
                                        >
                                            {{$t('appControl.uninstall')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="warning" 
                                        @click="appAction('upgradeApp', scope.row)" 
                                        v-if="scope.row.type === 'upgradeApp'"
                                        >
                                            {{$t('appControl.upgrade')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="primary" 
                                        @click="appAction('installApp', scope.row)" 
                                        v-if="scope.row.type === 'installApp'"
                                        >
                                            {{$t('appControl.install')}}
                                        </el-button>
                                    </el-button-group>
                                </span>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <el-tab-pane name="appStatusHistory">
                <span slot="label">
                    <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                    {{$t('appControl.appManagerHistory')}}
                </span>
                <app-history
                :statusType="singleInstallType"
                ref="appHistory"
                @isFinished="isFinished"
                :selectedAgentId="selectedAgentId"
                >
                </app-history>
            </el-tab-pane>
        </el-tabs>
    </div>
    
</template>


<script>
    import {appControl} from '@/assets/js/lwm2mMap'
    import {showSystemAppArray, disableAppArray} from "@/assets/js/constant"
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus } from '@/components/restfulapi/deviceStatusApi';
    import { androidControl } from '@/assets/js/lwm2mMap';
    import {getRepoAppsApi, getRepoApi} from '@/components/restfulapi/getRepoAppApi';
    import {getSession, checkSession, setSession } from '../../../assets/js/storage';
    import {getRepoSrcApi} from "../../restfulapi/serverConfigApi"
    import {singleInstallType, batchInstallType} from "../../../assets/js/constant";
    import appHistory from "./history"
    import cardTemp from "../../../common/cardTemp"

    export default{
        name: 'singleAppcontrol',
        data(){
            return {
                activeName: "appMarket",
                startApp: '',
                stopApp: '',
                enableApp: '',
                disableApp: '',
                appTableData: [],
                appOptions: [],
                disableAppArray: disableAppArray,

                appGroups:[{label: "Third Part", value: "third"},{label: "System", value: "system"}],
                defaultImg: 'this.src="' + 'static/imgs/default_apk.png' + '"',
                //history
                singleInstallType,
                //card
                appType: "uninstallApp",
                whitelist: false,
                listLoading: false,
                keywords: '',
            }
        },

        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },
            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            }
        },
        components: {
            appHistory,
 	        cardTemp
        },

        methods:{
            getSensorStatus(){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(this.selectedAgentId == ""){
                    console.error("selectAgentId is empty");
                    return;
                }
                let AppManagementInfoArray = [];
                let deviceAppArray = [];
                this.listLoading = true;
                getDeviceStatus(this.selectedAgentId, appControl["getAllAppInfo"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let apppackageinfo = JSON.parse(res.content.value);
                                let allAppData = apppackageinfo.data;
                                this.appOptions = allAppData;
                                if(Object.prototype.toString.call(allAppData) === "[object Array]" && allAppData.length > 0){
                                    allAppData.forEach((val) => {
                                        let uninstallAppObj = {
                                            type: 'uninstallApp',
                                            appname: val.appName,
                                            package: val.packageName,
                                            versioncode: val.versionCode,
                                            version: val.versionName,
                                            address: "",
                                            raid: "",
                                            isKioskApp: val.isKioskApp,
                                            inwhitelist: val.inWhiteList,
                                        };
                                        if(val.type === "third"){
                                            AppManagementInfoArray.push(uninstallAppObj);
                                        }
                                        deviceAppArray.push(uninstallAppObj);  
                                    })
                                }
                            }
                            
                            this.getRepoApps(AppManagementInfoArray, deviceAppArray);  
                        }     
                    })   
                })
            },
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_apk_val){
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    originData.forEach((obj_val, index) => {
                        if(obj_val.package === rem_apk_val){
                            originData.splice(index ,1)
                        }
                    })
                }else{
                     console.error("data is not array")
                     return;
                }
                
            },
            //get repo apps
            getRepoApps(AppManagementInfoArray, deviceAppArray){
                if(
                    Object.prototype.toString.call(AppManagementInfoArray) != "[object Array]" ||
                    Object.prototype.toString.call(deviceAppArray) != "[object Array]"
                ){
                    console.error("getRepoAppsparameter is not array")
                    return;
                }
                let token;
                let InstallAppManagementInfo = {};
                let UpgradeAppManagementInfo = {};
                this.listLoading = true;
                getRepoAppsApi().then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            let installappopt = "";
                            let upgradeappopt = "";
                            let lastVersionCode = [];
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    let version = val.versionName != null ? val.versionName:"";
                                    InstallAppManagementInfo = {
                                        type : "installApp", 
                                        darkname: val.repo.darkname,
                                        //install apk name
                                        appname: val.filename, 
                                        package: val.pkgname, 
                                        versioncode: val.versioncode, 
                                        version: val.versionname,
                                        address: val.address,
                                        raid: val.raid,
                                        isKioskApp: "false",
                                        inwhitelist: "false",
                                        };
                                    AppManagementInfoArray.push(InstallAppManagementInfo);
                         
                                    deviceAppArray.forEach((deviceapp_val, index) => {
                                        if(val.pkgname === deviceapp_val.package){
                                            // pop latest app info
                                            AppManagementInfoArray.pop();
                                            deviceAppArray[index].address = val.address;
                                            deviceAppArray[index].raid = val.raid;
                                            if (Number(val.versioncode) > Number(deviceapp_val.versioncode)){
                                                // remove install app  
                                                // this.removeObjInArray(AppManagementInfoArray, val.pkgname);
                                                UpgradeAppManagementInfo = {
                                                    type : "upgradeApp", 
                                                    darkname: val.repo.darkname,
                                                    appname: val.filename, 
                                                    package: val.pkgname, 
                                                    versioncode: val.versioncode, 
                                                    version: val.versionname,
                                                    address: val.address,
                                                    raid: val.raid,
                                                    isKioskApp: "false",
                                                    inwhitelist: "false",
                                                    };
                                                    //push update app 
                                                AppManagementInfoArray.push(UpgradeAppManagementInfo);     
                                                
                                            }
                                            
                                        }
                                    })
                                })
                            }else{
                                 console.error("data is not array")
                                 return;
                            }
                            
                        }
                        this.appTableData = AppManagementInfoArray.reverse();
                    })
                    
                })
            },

            filterType(value, row){
                return row.type === value ;
            },
            actionAppSensor(cid, appdata){
                if(appControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                if(!this.selectedAgentId){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }

                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("appControl."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        _g.openGlobalLoading();
                        execDeviceStatus(this.selectedAgentId, appControl[cid], appdata).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status == "CHANGED"){
                                    this.activeName = "appStatusHistory";
                                    this.$refs.appHistory.intervalGetAppStatusHistory();
                                }else{
                                    _g.handleError(res)
                                }
                            })
                        })
                    }
                })
            },

            setAppSensor(cid, setSensorVal){
                if(appControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                if(!setSensorVal){
                    return;
                }
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }
                if(cid === "uninstallApp" || cid === "disableApp"  || cid === "stopApp" ){
                    _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("appControl."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                        if (willfunc) {
                            _g.openGlobalLoading();
                            setDeviceStatus(this.selectedAgentId, appControl[cid], setSensorVal).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status == "CHANGED"){
                                        this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                            if(cid === "uninstallApp"){
                                                this.getSensorStatus();
                                            }                             
                                        })
                                    }else{
                                        _g.handleError(res)
                                    }
                                })
                                
                            })
                        }
                    })
                }else{
                    _g.openGlobalLoading();
                    setDeviceStatus(this.selectedAgentId, appControl[cid], setSensorVal).then((data) => {
                        handleResponse(data, (res) => {
                            if(res.status == "CHANGED"){
                                this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                    if(cid === "setKiosk"|| cid === "cancelKiosk"){
                                        this.getSensorStatus();
                                    }                             
                                })
                            }else{
                                _g.handleError(res)
                            }
                        })
                        
                    })
                }
            },

            isFinished(){
                if(this.selectedAgentId){
                    if(!this.isOffline){
                        this.getSensorStatus();

                    }

                }
            },

            appAction(cid, selectedAppData){

                let setsensorval, version;
                if(cid === "installApp" || cid === "upgradeApp"){
                    setsensorval = selectedAppData.address;
                    version = selectedAppData.version;
                }else{
                    setsensorval = selectedAppData.package;
                    this.setAppSensor(cid, setsensorval);
                    return;
                }
                let taskname = this.singleInstallType+ (new Date()).getTime();
                let appdata = {
                    taskname,
                    filename: selectedAppData.appname,
                    pkgname: selectedAppData.package,
                    versioncode: selectedAppData.versioncode,
                    version: version,
                    downloadpath: setsensorval,

                }
                this.actionAppSensor(cid, appdata)

                
            },

            handleCommand(command){
                let type=command.id
                let pkgname;
                if(this.appType === "system"|| this.appType==="desktop"){
                    pkgname = command.appInfo.packageName;
                }else{
                    pkgname = command.appInfo.package;
                }
                this.setAppSensor(type, pkgname)
            },


            getSrc(raid){
                if(raid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getAppIcon/"+raid;
                }else{
                    return 'static/imgs/default_apk.png';
                }
            },

            initData(){
                this.startApp = '';
                this.stopApp = '';
                this.enableApp = '';
                this.disableApp = '';
                this.appTableData = [];
                this.appOptions = [];
            },
            handleClick(tab){
                if(tab.name === "appStatusHistory"){
                    this.$emit("isDisabled", false)
                }else{
                    this.$emit("isDisabled", true)
                }
            },

            //get whitelist server status
            getServerStatus(){
                let sensor_key = "whitelist"
                getDeviceStatus(this.selectedAgentId, androidControl[sensor_key]).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let status = res.content.value;
                            if(typeof(status) === "boolean"){
                                this[sensor_key] = status;
                            }else if(status === "true" || status === "false"){
                                this[sensor_key] = status === "true"? true: false;
                            }else{
                                this[sensor_key] = status;
                            }
                        }
                    })
                })
                
            },
            disableOperation(){
                this.$swal("", this.$t('appControl.appNotInWhitelist'), 'info', {button: this.$t('global.confirm')});
            }
        },
        watch: {
            isSingleMode(val){
                if(val){
                    this.initData();
                }
            },
            selectedAgentId(val){
                if(this.isSingleMode) {
                    this.initData();

                    if(val&& !this.isOffline){
                        this.getSensorStatus();
                        this.getServerStatus();

                        // this.$refs.appHistory.intervalGetAppStatusHistory();
                    }
                }
            },
           
        },
        computed: {
            isShowSearchInput(){
                if(this.activeName === "appMarket"){
                    return true;
                }else{
                    return false;
                }
            }

        }
    }
</script>

<style lang="scss" scoped>
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }

    .search{
        position: absolute;
        top: 3px;
        right: 15px;
        width: 230px;
        z-index: 1001;
        
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
</style>



