<template>
    <div>
        <el-table
            :data="appStatusHistoryTableList"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="selectItem"
            @filter-change="filterStatus"
            :default-sort = "{prop: 'ts', order: 'descending'}"
            ref="appStatusHistoryTable"
            v-loading="listLoading" 

            >
            <el-table-column
            type="selection"
            min-width="50"
            :selectable="checkSelectable">
            </el-table-column>

            <el-table-column
            prop="devname"
            :label="$t('appControl.deviceName')"
            min-width="120"
            >
            </el-table-column>

            <el-table-column
            prop="taskname"
            :label="$t('appControl.taskName')"
            min-width="120"
            v-if="statusType == batchInstallType"
            >
            </el-table-column>

            <el-table-column
            prop="filename"
            :label="$t('appControl.apkName')"
            min-width="140"
            >
            </el-table-column>

            <el-table-column
            prop="versionname"
            :label="$t('appControl.versionName')"
            min-width="105"
            >
            </el-table-column>
            
            <el-table-column
            :label="$t('appControl.beginTime')"
            min-width="130"
            prop="ts"
                >
                <template slot-scope="scope">
                    {{scope.row.ts|time}}
                </template>
            </el-table-column>

            <el-table-column
            prop="target"
            :label="$t('appControl.function')"
            min-width="120"
            >
                <template slot-scope="scope">
                    {{scope.row.target|appStatusFunction}}
                </template>
            </el-table-column>
            <el-table-column
            column-key="status"
            prop="status"
            :label="$t('appControl.installStatus')"
            min-width="140"
            :filters="[{ text: $t('global.success'), value: 2 }, { text: $t('global.failed'), value: 1 }, {text: $t('global.running'), value: 0}]"
            filter-placement="bottom-end"
            >
                <template slot-scope="scope">
                    <img src="@/assets/imgs/loading1.gif" alt="" style="width:50px;height:10px;"v-if="scope.row.status==0">
                    <span v-if="scope.row.status==2" class="badge bg-green">{{$t('global.success')}}</span>
                    <span v-if="scope.row.status==1" class="badge bg-DimGray">{{$t('global.failed')}}</span>
                    <!-- {{scope.row.upstatus|AppStatus}} -->
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('appControl.errorStatus')"
            min-width="100"
            >
                <template slot-scope="scope">
                    {{(""+scope.row.errorcode)|appErrorStatus}}
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('appControl.action')"
            min-width="175"
            align="center">
                <template slot-scope="scope">
                    <div>
                        <el-button-group>
                            <el-button 
                                size="mini" 
                                type="danger"
                                @click="deleteHistory(scope.row.aisid)"
                                :disabled="scope.row.status==0"
                                >
                                    {{$t('global.delete')}}
                            </el-button>
                            <el-button 
                                size="mini" 
                                type="primary"
                                @click="reinstall(scope.row)"
                                :disabled="scope.row.status!=1"
                                >
                                    {{$t('appControl.reinstall')}}
                            </el-button>
                        </el-button-group>
                    </div>
                </template>
            </el-table-column>

        </el-table>

        <btn-group 
        :selectedData="multipleSelection" 
        :isDelete="true" 
        :isMove="false" 
        tid="aisid" 
        :delApi="batchDeleteAppStatusHistory" 
        @result="isSuccess" 
        v-show="isShowBtnGroup"
        class="fl m-t-10 cf">
        </btn-group>

        <el-button 
        v-loading="clearLoading" 
        size="small" 
        @click="clearAll()" 
        type="primary" 
        class="fl m-t-10 cf m-l-10"
            v-show="isShowBtnGroup">
        {{$t('appControl.clearAll')}}
        </el-button>

        <div class="m-t-10 cf fr">
            <el-pagination
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :page-size="limit"
            :current-page="currentPage"
            :total="dataCount"
            v-show="isshow"
            background
            >
            </el-pagination>
            
        </div> 
        
       
    </div>
</template>
<script>
    import {appControl} from '@/assets/js/lwm2mMap'
    import {reExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import{
    getAppStatusByTypeApi, 
    batchDeleteAppStatusHistoryApi, 
    deleteAppStatusHistoryApi, 
    deleteAppStatusHistoryAllByTypeApi,
    deleteAppStatusHistoryAllApi,
    getAppStatusOverviewApi,
    getAppStatusByTypeAndDevIdApi, 
    getAppStatusByTypeAndGroupApi,
    getAppStatusByTypeAndPageInGroupApi,
    getAppStatusByTypeAndPageAndStatusInGroupApi,
    getAppStatusByTypeAndPageInDevIdApi,
    getAppStatusByTypeAndPageAndStatusInDevIdApi,
    deleteAppStatusHistoryAllByDevIdAndTypeApi,
    deleteAppStatusHistoryAllByGroupAndTypeApi
    } from "@/components/restfulapi/appStatusApi"
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"

    import handleResponse from '@/components/restfulapi/handleResponse'
    import btnGroup from "../../../common/btn-group";
    import {singleInstallType, batchInstallType} from "../../../assets/js/constant"

export default {
    name: 'appcontrolHistory',
    data() {
        return {
            appStatusHistoryTableList: [],
            isshow: false,
            limit: 10,
            dataCount: 0,
            currentPage: 1,
            multipleSelection: [],
            isShowBtnGroup: false,
            clearLoading: false,
            batchDeleteAppStatusHistory:  batchDeleteAppStatusHistoryApi,
            intervalTime: 7000,

            isInterval: false,
            listLoading: false,
            singleInstallType,
            batchInstallType,
            statusArray: [],
        }
    },

    components: {
        btnGroup,
    },

    props: {
        statusType: {
            default: batchInstallType,
            type: String
        },

        selectedAgentId: {
            default: "",
            type: String
        },

        groupname: {
            default: "",
            type: String
        }
    },
    methods: {
        selectItem(val) {
            this.multipleSelection = val;
        },

        checkSelectable(row) {
            return !row.status==0;
        },

        handleCurrentChange(currentPage){
            if(currentPage){
                this.currentPage = currentPage;
            }else{
                this.currentPage = 1
            }
            this.intervalGetAppStatusHistory();
        },

        deleteHistory(aisid){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.listLoading = true;
                    deleteAppStatusHistoryApi(aisid).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetAppStatusHistory();
                                })
                            }else{
                                _g.handleError(res);
                            }
                        })
                    })
                }
            }) 
        },

        checkAppStatusData(){
            if(this.statusType == batchInstallType){
                getAppStatusByTypeAndGroupApi(this.groupname, this.statusType)
            }else{
                getAppStatusByTypeAndDevIdApi(this.selectedAgentId, this.statusType)
            }
        },

        getAppStatusHistoryByGroupname(){
            if(!this.groupname){
                console.error("[getAppStatusHistoryByGroupname]groupname is empty")
            }
            if(this.isInterval === false){
                this.listLoading = true;
            }
            
            if(this.statusArray.length > 0){
                getAppStatusByTypeAndPageAndStatusInGroupApi(this.groupname, this.statusType, this.currentPage, this.limit, this.statusArray).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignAppStatusData(res);
                    })
                })
            }else{
                getAppStatusByTypeAndPageInGroupApi(this.groupname, this.statusType, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignAppStatusData(res);
                    })
                })
            }
            
        },

        getAppStatusHistoryByDevid(){
            if(!this.selectedAgentId){
                console.error("[getAppStatusHistoryByDevid]selectedAgentId is empty")
            }

            if(this.isInterval === false){
                this.listLoading = true;
            }
            if(this.statusArray.length > 0){
                getAppStatusByTypeAndPageAndStatusInDevIdApi(this.selectedAgentId, this.statusType, this.currentPage, this.limit, this.statusArray).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignAppStatusData(res);
                    })
                })
            }else{
                getAppStatusByTypeAndPageInDevIdApi(this.selectedAgentId, this.statusType, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignAppStatusData(res);
                    })
                })
            }
        },

        assignAppStatusData(res){
            if(Object.prototype.toString.call(res)  === '[object Object]'&& Object.prototype.toString.call(res.data) === '[object Array]'){
                this.dataCount = res.count;
                this.appStatusHistoryTableList = res.data
                this.isshow = this.dataCount > this.limit;
                this.isShowBtnGroup = this.dataCount > 0;
                let isFinish = true;
                if(this.statusType == batchInstallType){
                    this.$emit("getStatusCount")
                }

                res.data.forEach((val) => {
                    if(val.status == 0){
                        isFinish = false;
                    }
                })
                if(isFinish){
                    
                    this.$emit("isFinished");
                    
                    window.clearInterval(_g.timer);
                    _g.timer = null;
                }
            }else{
                window.clearInterval(_g.timer);
                _g.timer = null;
            }
        },

        intervalGetAppStatusHistory(){
            if(this.statusType == batchInstallType){
                this.getAppStatusHistoryByGroupname();
                if(_g.timer === null){
                    _g.timer = window.setInterval(() => {
                        this.getAppStatusHistoryByGroupname();
                        this.isInterval = true;
                    },this.intervalTime)
                } 
            }else{
                this.getAppStatusHistoryByDevid();
                if(_g.timer === null){
                    _g.timer = window.setInterval(() => {
                        this.getAppStatusHistoryByDevid();
                        this.isInterval = true;
                    },this.intervalTime)
                }
            }
        },


        filterStatus(value){
            this.statusArray = value.status;
            this.currentPage = 1;
            if(_g.isArray(this.statusArray)){
                this.intervalGetAppStatusHistory();
            }
        },

        resetAppSensor(sensorId, selectedAgentId, appdata){
            this.listLoading = true;
            reExecDeviceStatus(selectedAgentId, sensorId, appdata).then((data) => {
                this.listLoading = false;
                handleResponse(data, (res) => {
                    if(res.status == "CHANGED"){
                        this.activeName = "appStatusHistory";
                        this.currentPage = 1;
                        this.intervalGetAppStatusHistory();
                    }else{
                        _g.handleError(res)
                    }
                })
                
            })
            
        },

        reinstall(appItem){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('appControl.reinstall'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    let appdata = {
                        taskname: appItem.taskname,
                        filename: appItem.filename,
                        pkgname: appItem.pkgname,
                        versioncode: appItem.versioncode,
                        version: appItem.versionname,
                        downloadpath: appItem.address,
                    }
                    this.resetAppSensor(appItem.target, appItem.deviceId, appdata);
                }
            })
        },

        //delete appstatus $emit
        isSuccess(result){
            if(result){
                this.intervalGetAppStatusHistory();
            }
        },

         //clear All history
        clearAll(){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.clearAll'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.clearLoading = true;
                    if(this.statusType == batchInstallType){
                        deleteAppStatusHistoryAllByGroupAndTypeApi(this.groupname, this.statusType).then((data) => {
                            this.clearLoading = false;
                            handleResponse((data), (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.intervalGetAppStatusHistory();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }else{
                        deleteAppStatusHistoryAllByDevIdAndTypeApi(this.selectedAgentId, this.statusType).then((data) => {
                            this.clearLoading = false;
                            handleResponse((data), (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.intervalGetAppStatusHistory();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }  
                }
            })

        },

        initData(){
            this.appStatusHistoryTableList = [];
            this.isShowBtnGroup= false;
            this.isshow= false;
            this.isInterval = false;
            this.currentPage = 1;
        }


    },

    created(){
        if(this.statusType == batchInstallType){
            this.$emit("getStatusCount")
        }
    },
    beforeDestroy() {
         window.clearInterval(_g.timer);
        _g.timer = null;
    },

    watch: {
        groupname(val){
            if(val){
                this.initData();
                this.checkAppStatusData();
                this.intervalGetAppStatusHistory();
                
            }  
},
        selectedAgentId(val){
            if(val){
                this.initData();
                this.checkAppStatusData();
                this.intervalGetAppStatusHistory();
                
            }  
},

    },
        
}
</script>
<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .badge{
        display: inline-block;
        min-width: 10px;
        padding: 5px 8px;
        font-size: 12px;
        font-weight: 700;
        line-height: 1;
        color: #fff;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        // background-color: #777;
        border-radius: 10px;
    }

    .el-loading-mask{
        top: 60px !important;
    }


    
</style>



