<template>
    <div v-show="!isSingleMode" class="appStatusContent p-r">
        <div class="search" v-show="isShowSearchInput"> 
            <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('appControl.keywordHolder')" >
            </el-input>
            <i class="fa fa-search search-icon" aria-hidden="true"></i>
        </div>

        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane name="appMarket">
                <span slot="label">
                    <i class="fa fa-sellsy m-r-5" aria-hidden="true"></i>
                    {{$t('appControl.appMarket')}}
                </span>
                <el-table
                    :data="appTableData.filter(data => !keywords || data.appname.toLowerCase().includes(keywords.toLowerCase()))"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading"
                    >

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img :src="getSrc(scope.row.raid)" :onerror="defaultImg" alt="no img" style="width:52px"/>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="appname"
                    :label="$t('appControl.appName')"
                    min-width="120"
                    >
                    </el-table-column>

                    <el-table-column
                    prop="package"
                    :label="$t('appControl.packageName')"
                    min-width="120"
                    sortable>
                    </el-table-column>

                    <el-table-column
                    prop="version"
                    :label="$t('appControl.versionName')"
                    min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="type"
                    :label="$t('appControl.action')"
                    min-width="120"
                    :filters="[{ text: $t('appControl.uninstall'), value: 'uninstallApp' }, { text: $t('appControl.install'), value: 'installApp' }, { text: $t('appControl.upgrade'), value: 'upgradeApp' }]"
                    :filter-method="filterType"
                    filter-placement="bottom-end"
                    >
                        <template slot-scope="scope">
                            <div>
                                <span>
                                    <el-button-group>
                                        <el-button 
                                        size="small" 
                                        type="danger" 
                                        @click="selectBatchInstallApp('uninstallApp', scope.row)"
                                        v-if="scope.row.type === 'uninstallApp'"
                                        :disabled="disableAppArray.indexOf(scope.row.package)>-1"
                                        >
                                            {{$t('appControl.uninstall')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="warning" 
                                        @click="selectBatchInstallApp('upgradeApp', scope.row)" 
                                        v-if="scope.row.type === 'upgradeApp'"
                                        >
                                            {{$t('appControl.upgrade')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="primary" 
                                        @click="selectBatchInstallApp('installApp', scope.row)" 
                                        v-if="scope.row.type === 'installApp'"
                                        >
                                            {{$t('appControl.install')}}
                                        </el-button>
                                    </el-button-group>
                                </span>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
           
            <el-tab-pane name="appStatusHistory">
                <span slot="label">
                    <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                    {{$t('appControl.appManagerHistory')}}
                </span>
                <app-history
                :statusType="batchInstallType"
                ref="appHistory"
                @getStatusCount="getBatchAppStatusCount"
                :groupname="groupname"
                @isFinished="isFinished"
                >
                </app-history>

            </el-tab-pane>
        </el-tabs>
         <el-dialog :visible.sync="dialogTasknameVisible" @close="initApkInfo()" :close-on-click-modal="false">
            <div slot="title" class="dialog-title">
                <i class="fa fa-level-up m-r-10" ></i>
                {{$t('appControl.batchInstallApk')}}
            </div>
            <el-form :model="appStatusForm" :rules="rules" ref="appStatusForm">
                <el-form-item :label="$t('label.taskName')" label-width="230px" prop="taskname">
                    <el-input v-model.trim="appStatusForm.taskname" autocomplete="off" class="max-w-350" :placeholder="$t('appControl.installAppTaskName')"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogTasknameVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="batchInstallApp()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
        <div class="appStatusCount" v-show="isShowOverview">
            <span>{{$t('label.taskName')}} {{local_taskname}}</span>
            <span>{{$t('appControl.total')}} {{total}}</span>
            <span>{{$t('appControl.deploying')}} {{installing}}</span>
            <span>{{$t('appControl.success')}} {{success}}</span>
            <span>{{$t('appControl.failed')}} {{failed}}</span>
        </div>
    </div>
</template>

<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .appStatusContent{
        position: relative;
        .appStatusCount{
            position: absolute;
            top: 10px;
            right: 6px;
            span{
                font-weight: 500;
                font-size: 11px;
                &:nth-child(1){
                    color: $primary-color;
                }
                &:nth-child(2){
                    border-left: 2px solid #ddd;
                    padding-left: 3px; 
                    color: $info-color;
                }
                &:nth-child(3){
                    color: $warn-color;
                }
                &:nth-child(4){
                    color: $success-color;
                }
                &:nth-child(5){
                    color: $darkgray-color;
                }
            }
            
        }
    }

    .search{
        position: absolute;
        top: 3px;
        right: 15px;
        width: 230px;
        z-index: 1001;
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }

</style>

<script>
    import {appControl, androidControl} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {showSystemAppArray, disableAppArray} from "@/assets/js/constant"

    import {getRepoAppsApi, getRepoApi} from '@/components/restfulapi/getRepoAppApi'
    import{getAppStatusOverviewApi} from "@/components/restfulapi/appStatusApi"

    import {getDeviceStatus, setDeviceStatus, batchExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"
    import {getRepoSrcApi} from "../../restfulapi/serverConfigApi"


    import {singleInstallType, batchInstallType} from "../../../assets/js/constant"
    import appHistory from "./history"
    import cardTemp from "../../../common/cardTemp"
    export default{
        name: 'batchAppcontrol',
        data(){
            let validateTaskname = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "appMarket",
                appTableData: [],

                defaultImg: 'this.src="' + 'static/imgs/default_apk.png' + '"',
                dialogTasknameVisible: false,
                //app status history
                filename: '',
                pkgname: '',
                version: '',
                address: '',
                versioncode: '',
                //batch installapp
                appStatusForm:{
                    taskname: '',
                },
                rules:{
                    taskname: {required: true, validator: validateTaskname, trigger: 'blur'}
                },

                batchInstallType: batchInstallType,
                cid: "",
                appOptions: [],
                disableAppArray: disableAppArray,
                appType: "uninstallApp",
                whitelist: false,
                //installstatus count show
                isShowOverview: false,
                local_taskname: '',
                success: 0,
                installing: 0,
                failed: 0,
                listLoading: false,
                keywords: '',

            }
        },

        props:{
            selectedAgentsData: {
                type: Array,
                default: []
            },
            groupname: {
                type: String,
                default: ""
            }, 
            isSingleMode: {
                type: Boolean,
                default: true
            }
        },

        components: {
            appHistory,
            cardTemp
        },

        methods:{
            handleCommand(command){
                let appInfo=command.appInfo;
                this.cid = command.id;
                //package is diff in system and third
                if(this.appType === "system"|| this.appType==="desktop"){
                    this.address = "";
                    this.filename = appInfo.appName;
                    this.pkgname = appInfo.packageName;
                    this.version = appInfo.versionName;
                    this.versioncode = appInfo.versionCode;
                }else{
                    this.address = appInfo.address;
                    this.filename = appInfo.appname;
                    this.pkgname = appInfo.package;
                    this.version = appInfo.version;
                    this.versioncode = appInfo.versioncode;
                }

                this.appStatusForm.taskname = "";
                this.dialogTasknameVisible = true;

            },

            filterType(value, row){
                return row.type === value ;
            },

            getSensorStatus(){
                if(this.isSingleMode) {
                    console.error("Cannot operate in a single mode");
                    return;
                }
                if(this.selectedAgentsData.length == 0){
                    console.error("getSensorStatus: agentData is null");
                    return;
                }
                let AppManagementInfoArray = [];
                let deviceAppArray = [];
                this.listLoading = true;
                getDeviceStatus(this.selectedAgentsData[0].value, appControl["getAllAppInfo"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let apppackageinfo = JSON.parse(res.content.value);
                                let allAppData = apppackageinfo.data;
                                this.appOptions = allAppData;
                                if(Object.prototype.toString.call(allAppData) === "[object Array]" && allAppData.length > 0){
                                    allAppData.forEach((val) => {
                                        let uninstallAppObj = {
                                            type: 'uninstallApp',
                                            appname: val.appName,
                                            package: val.packageName,
                                            versioncode: val.versionCode,
                                            version: val.versionName,
                                            address: "",
                                            raid: "",
                                            isKioskApp: val.isKioskApp,
                                            inwhitelist: val.inWhiteList,
                                        };
                                        if(val.type === "third"){
                                            AppManagementInfoArray.push(uninstallAppObj);
                                        }
                                        deviceAppArray.push(uninstallAppObj);                            
                                            
                                    })
                                }
                            }
                        }
                        this.getRepoApps(AppManagementInfoArray, deviceAppArray);  
                    })   
                })
            },
            
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_apk_val){
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    originData.forEach((obj_val, index) => {
                        if(obj_val.package === rem_apk_val){
                            originData.splice(index ,1)
                        }
                    })
                }else{
                     console.error("data is not array")
                     return;
                }
                
            },
            //get repo apps
            getRepoApps(AppManagementInfoArray, deviceAppArray){
                if(
                    Object.prototype.toString.call(AppManagementInfoArray) != "[object Array]" ||
                    Object.prototype.toString.call(deviceAppArray) != "[object Array]"
                ){
                    console.error("getRepoApps parameter is not array")
                    return;
                }
                let token;
                let InstallAppManagementInfo = {};
                let UpgradeAppManagementInfo = {};
                this.listLoading = true;
                getRepoAppsApi().then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            let installappopt = "";
                            let upgradeappopt = "";
                            let lastVersionCode = [];
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    let version = val.versionName != null ? val.versionName:"";
                                    InstallAppManagementInfo = {
                                        type : "installApp", 
                                        darkname: val.repo.darkname,
                                        //install apk name
                                        appname: val.filename, 
                                        package: val.pkgname, 
                                        versioncode: val.versioncode, 
                                        version: val.versionname,
                                        address: val.address,
                                        raid: val.raid,
                                        isKioskApp: "false",
                                        inwhitelist: "false",
                                        };
                                    AppManagementInfoArray.push(InstallAppManagementInfo);
                         
                                    deviceAppArray.forEach((deviceapp_val, index) => {
                                        if(val.pkgname === deviceapp_val.package){
                                            // pop latest app info
                                            AppManagementInfoArray.pop();
                                            deviceAppArray[index].address = val.address;
                                            deviceAppArray[index].raid = val.raid;
                                            if (Number(val.versioncode) > Number(deviceapp_val.versioncode)){
                                                // remove install app  
                                                // this.removeObjInArray(AppManagementInfoArray, val.pkgname);
                                                UpgradeAppManagementInfo = {
                                                    type : "upgradeApp", 
                                                    darkname: val.repo.darkname,
                                                    appname: val.filename, 
                                                    package: val.pkgname, 
                                                    versioncode: val.versioncode, 
                                                    version: val.versionname,
                                                    address: val.address,
                                                    raid: val.raid,
                                                    isKioskApp: "false",
                                                    inwhitelist: "false",
                                                    };
                                                    //push update app 
                                                AppManagementInfoArray.push(UpgradeAppManagementInfo);     
                                                
                                            }
                                            
                                        }
                                    })
                                })
                            }
                            
                        }
                        this.appTableData = AppManagementInfoArray.reverse();
                    })
                    
                })
            },
	
            //batch install app
            batchSetAppSensor(cid, setSensorVal){
                if(appControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                _g.openGlobalLoading();
                
                let appdata = {
                    groupname: this.groupname,
                    taskname: this.appStatusForm.taskname,
                    filename: this.filename,
                    pkgname: this.pkgname,
                    version: this.version,
                    versioncode: this.versioncode,
                    downloadpath: setSensorVal,
                }  
                let taskname = this.appStatusForm.taskname;
                this.dialogTasknameVisible = false;                  
                batchExecDeviceStatus(appControl[cid], appdata).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status == "CHANGED"){
                            //show overview
                            setLocal("local_taskname", taskname);
                            this.activeName = "appStatusHistory";
                            this.$refs.appHistory.intervalGetAppStatusHistory();
                        }else{
                            _g.handleError(res);
                        }
                    })
                    
                })        
            
                               
            },

            batchInstallApp(){
                if(this.selectedAgentsData.length === 0){
                    this.$swal("", this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')});
                    return;
                }
                this.$refs.appStatusForm.validate((valid) => {
                    if(valid) {
                        this.batchSetAppSensor(this.cid, this.address);
                        
                    }
                })
            },
            
            selectBatchInstallApp(type, selectedAppData){
                this.cid = type;
                this.address = selectedAppData.address;
                this.filename = selectedAppData.appname;
                this.pkgname = selectedAppData.package;
                this.version = selectedAppData.version;
                this.versioncode = selectedAppData.versioncode;
                this.appStatusForm.taskname = "";
                this.dialogTasknameVisible = true;
            },

            initApkInfo(){
                this.filename = "";
                this.pkgname = "";
                this.version = "";
                this.address = "";
                this.appStatusForm.taskname = "";
                this.cid = "";
            },

             //get updatedp status count
            getBatchAppStatusCount(){
                let local_taskname = getLocal("local_taskname");
                if(!local_taskname){
                    console.error("getBatchAppStatusCount:localStorage(local_taskname) is null")
                    this.isShowOverview = false;
                    return;
                }
                getAppStatusOverviewApi(local_taskname, batchInstallType).then((data) => {
                    handleResponse((data), (res) => {
                        if(res){
                            this.local_taskname = local_taskname;

                            this.installing = res.installing;
                            this.failed = res.fail;
                            this.success = res.success;
                            if(this.total > 0 &&  this.activeName === "appStatusHistory"){
                                this.isShowOverview = true;
                            }else{
                                this.isShowOverview = false;
                            }
                        }
                    })
                })
            },
            
            initData(){
                this.appTableData = [];
            },

            getSrc(raid){

                if(raid){
                     let repourl =  getRepoSrcApi();
                    return repourl+"/getAppIcon/"+raid;
                }else{
                    return 'static/imgs/default_apk.png';
                }                
            },
            handleClick(tab){
                if(tab.name === "appStatusHistory"){
                    this.getBatchAppStatusCount();
                }else{
                    this.isShowOverview = false;
                }
            },
            isFinished(){
                this.getSensorStatus();  
            },
            //get whitelist server status
            getServerStatus(){
                if(this.selectedAgentsData.length == 0){
                    console.error("getServerStatus: agentData is null");
                    return;
                }
                let sensor_key = "whitelist"
                getDeviceStatus(this.selectedAgentsData[0].value, androidControl[sensor_key]).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let status = res.content.value;
                            if(typeof(status) === "boolean"){
                                this[sensor_key] = status;
                            }else if(status === "true" || status === "false"){
                                this[sensor_key] = status === "true"? true: false;
                            }else{
                                this[sensor_key] = status;
                            }
                        }
                    })
                })
                
            },

            disableOperation(){
                this.$swal("", this.$t('appControl.appNotInWhitelist'), 'info', {button: this.$t('global.confirm')});
            },

        },
       
        computed: {

            isShowSearchInput(){
                if(this.activeName === "appMarket"){
                    return true;
                }else{
                    return false;
                }
            },
            total(){
                return  this.success+ this.installing+ this.failed;
            }

        },
       
        watch: {
            isSingleMode(val){
                if(!val){
                    this.initData();
                }
            },
            groupname(val){
                if(!this.isSingleMode&& val){
                    this.initData();
                    this.getSensorStatus();
                    this.getServerStatus();
                }
            }
        }

    }
</script>


